/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-confusing-arrow */
/* eslint-disable multiline-ternary */
import { Fragment, useState, useEffect, useContext } from "react"
import { useTranslation } from "react-i18next"
import { AbilityContext } from "@src/utility/context/Can"
import ModalAddEdit from "./modal"
import { neutralizeBack, revivalBack } from "@src/utility/Utils"
import {
  storeName,
  pageTitle,
  serverSideGrid,
  detailFromGrid,
  getApiAfterPut,
  getApiAfterPost,
  getApiAfterDelete,
  getSummarysData,
  getAlwaysGrid,
  getDataList,
  getDataById,
  setDataById,
  deleteData,
  deleteSelectedRowData,
  setDataList,
  setDataListNested,
  putData
} from "./store"
import { useDispatch, useSelector } from "react-redux"
import { Edit2, Trash2, Plus } from "react-feather"

import { getUserData } from "@utils"

import UILoader from "@components/ui-loader"
import Spinner from "@components/spinner/Loading-spinner"
import { ThemeColors } from "@src/utility/context/ThemeColors"
import toast from "react-hot-toast"
import { Card, Button, UncontrolledTooltip, CardText } from "reactstrap"
import Swal from "sweetalert2"
import withReactContent from "sweetalert2-react-content"
const MySwal = withReactContent(Swal)
import "@styles/react/libs/react-select/_react-select.scss"
import "@styles/react/libs/tables/react-dataTable-component.scss"
import SortableTree from "@nosferatu500/react-sortable-tree"
import { NoDataDisplay, Loader } from "@src/shared/CommonComponents"
import Avatar from "@components/avatar"

const List = (searchString) => {
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const { t } = useTranslation()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const ability = useContext(AbilityContext)
  const [loading, setLoading] = useState(false)
  const [loadingText, setLoadingText] = useState("Please Wait")
  const [showModal, setShowModal] = useState(false)
  const [formState, setFormState] = useState(null)
  const [currentDataAdd, setCurrentDataAdd] = useState(null)

  const [searchFocusIndex, setSearchFocusIndex] = useState(0)
  const [searchFoundCount, setSearchFoundCount] = useState(null)

  const { colors } = useContext(ThemeColors)

  const handleEdit = (rowData) => {
    setFormState("edit")
    if (!detailFromGrid) {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(getDataById(rowData.id)).then((res) => {
        if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        } else {
          setLoading(false)
          setShowModal(true)
        }
      })
    } else {
      setLoadingText(
        `${t("Loading")} ${t("Detail")} ${t("Data")} ${t(pageTitle)}`
      )
      setLoading(true)
      dispatch(setDataById(rowData)).then(() => {
        setLoading(false)
        setShowModal(true)
      })
    }
  }

  const handleDelete = async (id) => {
    const result = await MySwal.fire({
      title: `${t("Are you sure?")}`,
      text: `${t("You won't be able to revert")} ${t(pageTitle)}!`,
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: `${t("Yes, Delete")} ${t(pageTitle)}!`,
      cancelButtonText: `${t("Cancel")}`,
      customClass: {
        confirmButton: "btn btn-primary",
        cancelButton: "btn btn-outline-danger ms-1"
      },
      buttonsStyling: false
    })
    if (result.value) {
      setLoadingText(`${t("Deleting")} Data ${t(pageTitle)}`)
      setLoading(true)
      dispatch(deleteData(id)).then((res) => {
        // console.log(res)
        if (res.meta.requestStatus === "fulfilled") {
          setLoading(false)
          toast.success(`${t("Successfully deleted")} ${t(pageTitle)}`, {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          })
        } else if (res.meta.requestStatus === "rejected") {
          setLoading(false)
          toast.error(res.payload.message, {
            style: {
              padding: "16px",
              color: colors.danger.main,
              border: `1px solid ${colors.danger.main}`
            },
            iconTheme: {
              primary: colors.danger.main
            }
          })
        }
      })
    }
  }

  const toggleSidebar = (row = null) => {
    // console.log(row.id)
    setCurrentDataAdd(row)
    setFormState("add")
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    neutralizeBack(handleCloseModal)
  }

  const handleCloseModal = () => {
    setSidebarOpen(!sidebarOpen)
    setShowModal(!showModal)
    revivalBack()
  }

  const handleChange = (tree) => {
    // console.log(tree)
    dispatch(setDataListNested(tree))
  }

  const eventCanDragNode = (args) => {
    // console.log(args)
    if (args.node.is_app_header) {
      return false
    }
    return true
  }

  const onMoveNode = ({
    treeData,
    node,
    nextParentNode,
    prevPath,
    prevTreeIndex,
    nextPath,
    nextTreeIndex
  }) => {
    // console.log(nextParentNode)
    const data = {
      name: node.name,
      id: node.id,
      parent_id: nextParentNode.id
    }
    updateDataToApi(data, true)
  }

  const updateDataToApi = (data, notReload = false) => {
    // console.log(data)
    setLoadingText(`${t("Updating")} ${t(pageTitle)} Data`)
    if (notReload) data["notReload"] = true
    const nameMsg = data.name
    delete data["name"]
    dispatch(putData(data, notReload)).then((res) => {
      // console.log(res)
      if (res.meta.requestStatus === "fulfilled") {
        toast.success(
          `${t("Successfully Updated")} ${t(pageTitle)} ${nameMsg}`,
          {
            style: {
              padding: "16px",
              color: colors.success.main,
              border: `1px solid ${colors.success.main}`
            },
            iconTheme: {
              primary: colors.success.main
            }
          }
        )
      } else if (res.meta.requestStatus === "rejected") {
        toast.error(res.payload.message, {
          style: {
            padding: "16px",
            color: colors.danger.main,
            border: `1px solid ${colors.danger.main}`
          },
          iconTheme: {
            primary: colors.danger.main
          }
        })
      }
    })
  }

  const customSearchMethod = ({ node, searchQuery }) => {
    return (
      searchQuery &&
      node.name.toLowerCase().indexOf(searchQuery.toLowerCase()) > -1
    )
  }

  const selectPrevMatch = () =>
    setSearchFocusIndex(
      searchFocusIndex !== null
        ? (searchFoundCount + searchFocusIndex - 1) % searchFoundCount
        : searchFoundCount - 1
    )

  const selectNextMatch = () =>
    setSearchFocusIndex(
      searchFocusIndex !== null ? (searchFocusIndex + 1) % searchFoundCount : 0
    )

  useEffect(() => {
    // console.log(store)
    if (
      (store.dataNested.length === 0 && !store.isLoadingNested) ||
      getAlwaysGrid
    ) {
      setLoadingText(`${t("Loading Data")} ${t(pageTitle)}`)
      dispatch(getDataList({}))
    }
  }, [dispatch, store.dataNested.length > 0])

  return (
    <Fragment>
      <UILoader blocking={store.isLoadingNested} loader={<Loader />}>
        <Card className="overflow-hidden">
          <div className="react-dataTable react-dataTable-selectable-rows">
            <div
              style={{
                height: 1000,
                marginLeft: 20,
                marginRight: 20,
                marginTop: 40
              }}
            >
              <SortableTree
                treeData={store.dataNested}
                onChange={handleChange}
                // onMoveNode={onMoveNode}
                // canDrag={eventCanDragNode}
                canDrag={false}
                searchMethod={customSearchMethod}
                searchQuery={searchString.searchString}
                searchFocusOffset={searchFocusIndex}
                searchFinishCallback={(matches) => {
                  setSearchFoundCount(matches.length)
                  setSearchFocusIndex(
                    matches.length > 0 ? searchFocusIndex % matches.length : 0
                  )
                }}
                // onlyExpandSearchedNodes
                maxDepth={10}
                generateNodeProps={({ node, path }) => ({
                  style: {
                    fontSize: node.is_app_header ? `16px` : "14px",
                    fontWeight: node.is_app_header ? `bold` : "normal"
                  },
                  title: (
                    <div className="d-flex justify-content-left align-items-center">
                      {
                        node?.attributes?.logo && (
                          <img
                            className="me-1"
                            src={node.attributes.logo}
                            // width="60"
                            height="25"
                          />
                        )
                        // : (
                        //   <Avatar
                        //     initials
                        //     className="me-1"
                        //     color={"light-primary"}
                        //     content={node.name || ""}
                        //   />
                        // )
                      }
                      <div className="d-flex flex-column">
                        <span className="fw-bolder">
                          {/* {node.code}  */}
                          {node.name}
                        </span>
                      </div>
                    </div>
                  ),
                  buttons: [
                    ability.can(
                      "post",
                      sessionStorage.getItem("current_page_perm")
                    ) ? (
                      <>
                        <Button.Ripple
                          className="btn-icon rounded-circle"
                          color="flat-primary"
                          id="btn-add-child"
                          onClick={(e) => {
                            toggleSidebar(node)
                          }}
                        >
                          <Plus size={16} />
                        </Button.Ripple>
                        <UncontrolledTooltip
                          placement="left"
                          target="btn-add-child"
                        >
                          {t("Add Child")}
                        </UncontrolledTooltip>
                      </>
                    ) : null,
                    ability.can(
                      "update",
                      sessionStorage.getItem("current_page_perm")
                    ) &&
                    !node.is_app_header &&
                    !node.disableEdit ? (
                      <>
                        <Button.Ripple
                          className="btn-icon rounded-circle"
                          color="flat-info"
                          id="btn-edit"
                          onClick={(e) => {
                            e.preventDefault()
                            handleEdit(node)
                          }}
                        >
                          <Edit2 size={16} />
                        </Button.Ripple>
                        <UncontrolledTooltip placement="left" target="btn-edit">
                          {t("Edit")}
                        </UncontrolledTooltip>
                      </>
                    ) : null,
                    ability.can(
                      "delete",
                      sessionStorage.getItem("current_page_perm")
                    ) &&
                    !node.disableDelete &&
                    node?.children?.length === 0 ? (
                      <>
                        <Button.Ripple
                          id="btn-delete"
                          className="btn-icon rounded-circle"
                          color="flat-danger"
                          onClick={(e) => {
                            e.preventDefault()
                            handleDelete(node.id)
                          }}
                        >
                          <Trash2 size={16} />
                        </Button.Ripple>
                        <UncontrolledTooltip
                          placement="left"
                          target="btn-delete"
                        >
                          {t("Delete")}
                        </UncontrolledTooltip>
                      </>
                    ) : null
                  ]
                })}
              />
            </div>
          </div>
        </Card>
      </UILoader>

      {showModal && (
        <ModalAddEdit
          formState={formState}
          show={showModal}
          setShow={toggleSidebar}
          detailPage={false}
          setFormState={setFormState}
          currentData={currentDataAdd}
        />
      )}
    </Fragment>
  )
}

export default List
